html,body,ul,li,span,a,p,img,tr,td,h1,h2,h3,h4,h5,h6 {
    padding: 0;
    margin: 0;
}

html,body {
    width: 100%;
    height: 100%;
    font-family: '\5FAE\8F6F\96C5\9ED1';
}

ul,li {
    list-style-type: none;
}

a {
   text-decoration: none;
}

#root {
  height: 100%;
}

.left {
    float: left;
}

.right {
    float: right;
}

.clear {
    clear: both;
}

.drip {
    width: 100%;
    height: 100%;
    display: flex;
    min-width: 1920px;
    max-width: 1920px;
    margin: 0 auto;
}

.drip_left {
    flex: 0.3 1;
    height: 100%;
    border-right: 1px solid #fff;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
}

.drip_right {
    flex: 0.7 1;
    height: 100%;
    overflow: auto;
}

.drip_l_nav {
    width: 100%;
    height: 80px;
}

.drip_l_nav li {
    width: 10%;
    float: left;
    text-align: center;
    line-height: 80px;
    font-weight: bold;
    cursor: pointer;
}

.drip_l_nav li a {
    color: #212121;
}

.drip_l_nav #portrait {
    height: 40px;
    position: relative;
    margin-right: 20px;
}

.drip_l_nav #portrait img {
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 40px;
    overflow: hidden;
}

.drip_l_nav #menu {
    display: block;
    width: 30px;
    height: 30px;
    float: right;
    margin: 22px 15px 0 0;
    background: url(data:image/png;base64,) no-repeat center center;
    background-size: 25px 25px;
    position: relative;
}

.drip_l_content {
    margin-top: 60%;
    text-align: center;
}

.drip_l_content h2 {
    text-align: center;
    margin-bottom: 10px;
}

.drip_l_content h2 img {
    width: 35px;
    height: 20px;
    margin-top: 5px;
}

.drip_l_content span {
    margin-top: 25px;
    font-size: 14px;
    color: #61C6F1;
}

.drip_right .drip_r_content {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
}

.drip_right .drip_r_content li {
    width: 25%;
    height: 300px;
    text-align: center;
    overflow: hidden;
    float: left;
    padding: 30px;
    margin-top: 30px;
    margin-left: 3%;
    border: 1px solid #61C6F1;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
    position: absolute;
    cursor: pointer;
    border-radius: 5px;
}

.drip_right .drip_r_content li:nth-child(2n) {
    background: #F7F7F7;
}

.drip_right .drip_r_content li h3 {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 15px;
}

.drip_right .drip_r_content li h3 a {
    color: #000;
}

.drip_right .drip_r_content li sapn {
    color: #999B99;
}

.drip_right .drip_r_content li img {
    display: block;
    width: 45%;
    height: 200px;
    position: absolute;
    left: 50%;
    margin-top: 20px;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    border: 1px solid #fff;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
    overflow: hidden;
}

.drip_left .menuList {
    width: 300px;
    display: inline-block;
    border: 1px solid #fff;
    position: absolute;
    left: -150px;
    top: 30px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
    background: #fff;
}

.drip_left .menuList li {
    width: 100%;
}

.drip_left .menuList li:hover {
    background: #61C6F1;
    color: #fff;
}
.content {
    overflow-x: hidden;
    padding: 60px 30px;
}

.content .title {
    text-align: center;
    margin-bottom: 30px;
}

.content .msg {
    margin: 0 auto;
}

.content .msg span{
    display: block;
}

.content .text {
    clear: both;
    margin-top: 80px;
}


